"use client";

import { useState, useEffect } from "react";
import Link from "next/link";
import { 
  ArrowRight, 
  Sparkles, 
  Hourglass, 
  Globe, 
  CheckCircle2, 
  Loader2,
  Lock,
  Ticket
} from "lucide-react";
import Logo from "@/components/Logo";

export default function EarlyAccessPage() {
  const [email, setEmail] = useState("");
  const [status, setStatus] = useState<'idle' | 'loading' | 'success'>('idle');
  const [spotsLeft, setSpotsLeft] = useState(142); 

  useEffect(() => {
    const interval = setInterval(() => {
      setSpotsLeft((prev) => (prev > 12 ? prev - Math.floor(Math.random() * 3) : 12));
    }, 5000);
    return () => clearInterval(interval);
  }, []);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    setStatus('loading');
    setTimeout(() => {
      setStatus('success');
    }, 2000);
  };

  return (
    <div className="min-h-screen bg-[#FAFAFA] text-slate-900 flex flex-col relative overflow-hidden font-sans selection:bg-black selection:text-white">
      
      {/* --- AMBIENT BACKGROUND (Light Mode Version) --- */}
      {/* Subtle Grey/Blue Gradients instead of Dark Aurora */}
      <div className="absolute top-[-20%] left-[-10%] w-[50vw] h-[50vw] bg-slate-200/40 rounded-full blur-[120px]"></div>
      <div className="absolute bottom-[-10%] right-[-10%] w-[40vw] h-[40vw] bg-indigo-50/60 rounded-full blur-[100px]"></div>
      
      {/* Grid Texture - Same as Manifesto for Consistency */}
      <div className="absolute inset-0 bg-[linear-gradient(to_right,#e5e5e5_1px,transparent_1px),linear-gradient(to_bottom,#e5e5e5_1px,transparent_1px)] bg-[size:4rem_4rem] [mask-image:radial-gradient(ellipse_60%_60%_at_50%_50%,#000_70%,transparent_100%)] z-0"></div>

      {/* --- HEADER --- */}
      <header className="relative z-20 px-6 py-8 flex justify-between items-center max-w-7xl mx-auto w-full">
        <Link href="/" className="hover:opacity-70 transition-opacity">
           <Logo variant="full" height={28} className="text-slate-900" />
        </Link>
        <div className="flex items-center gap-2 px-3 py-1 bg-white border border-slate-200 rounded-full text-[10px] font-mono tracking-widest uppercase text-slate-500 shadow-sm">
           <div className="w-1.5 h-1.5 bg-emerald-500 rounded-full animate-pulse"></div>
           Batch #03 Live
        </div>
      </header>

      {/* --- MAIN CONTENT --- */}
      <main className="flex-1 flex flex-col items-center justify-center px-4 relative z-10">
        
        <div className="w-full max-w-lg relative perspective-[1000px]">
          
          <div className="relative bg-white/80 backdrop-blur-xl border border-white/50 rounded-3xl p-8 md:p-12 shadow-[0_20px_60px_-15px_rgba(0,0,0,0.05),0_0_0_1px_rgba(0,0,0,0.02)]">
            
            {/* STATE: SUCCESS (The Black Card Ticket) */}
            {status === 'success' ? (
               <div className="text-center animate-in fade-in slide-in-from-bottom-4 duration-500">
                  <div className="inline-flex items-center gap-2 px-3 py-1 rounded-full bg-emerald-50 border border-emerald-100 text-emerald-700 text-xs font-bold mb-6">
                     <CheckCircle2 className="w-3.5 h-3.5" />
                     Confirmed
                  </div>
                  
                  <h2 className="text-2xl md:text-3xl font-bold mb-3 tracking-tight text-slate-900">
                     Akses Diamankan.
                  </h2>
                  <p className="text-slate-500 mb-8 text-sm">
                     Undangan resmi telah dikirim ke <span className="font-bold text-slate-900">{email}</span>.
                  </p>
                  
                  {/* THE TICKET VISUAL - Premium Black Card */}
                  <div className="relative w-full aspect-[1.8/1] bg-[#0F0F0F] rounded-xl p-6 text-white text-left shadow-2xl shadow-slate-300 transform transition-transform hover:scale-[1.02] cursor-default overflow-hidden group">
                     {/* Card Texture */}
                     <div className="absolute inset-0 opacity-20 bg-[url('https://grainy-gradients.vercel.app/noise.svg')]"></div>
                     
                     <div className="relative z-10 flex flex-col justify-between h-full">
                        <div className="flex justify-between items-start">
                           <div className="space-y-1">
                              <div className="text-[9px] font-mono uppercase tracking-[0.2em] text-slate-400">Access Pass</div>
                              <div className="text-lg font-bold">PRIVATE BETA</div>
                           </div>
                           <Ticket className="w-6 h-6 text-slate-600" />
                        </div>

                        <div className="flex justify-between items-end">
                           <div className="flex items-center gap-3">
                              <div className="w-8 h-8 rounded bg-white/10 backdrop-blur-sm flex items-center justify-center text-[10px] font-bold">
                                 {email.charAt(0).toUpperCase()}
                              </div>
                              <div className="text-xs text-slate-400 font-mono">{email}</div>
                           </div>
                           <div className="text-right">
                              <div className="text-[9px] font-mono uppercase tracking-[0.2em] text-slate-500">Queue ID</div>
                              <div className="text-xl font-mono font-bold tracking-tight">#8,942</div>
                           </div>
                        </div>
                     </div>

                     {/* Holographic Shine */}
                     <div className="absolute inset-0 bg-gradient-to-tr from-transparent via-white/10 to-transparent translate-x-[-100%] group-hover:translate-x-[100%] transition-transform duration-1000 ease-in-out pointer-events-none"></div>
                  </div>

                  <div className="mt-8">
                     <Link href="/" className="text-xs text-slate-400 hover:text-slate-900 transition-colors flex items-center justify-center gap-2 font-medium">
                        <ArrowRight className="w-3 h-3 rotate-180" />
                        Kembali ke Homepage
                     </Link>
                  </div>
               </div>
            ) : (
               /* STATE: FORM (The Clean Invite) */
               <div className="text-center">
                  <div className="w-12 h-12 bg-slate-50 border border-slate-100 rounded-xl flex items-center justify-center mx-auto mb-6 shadow-sm">
                     <Sparkles className="w-5 h-5 text-slate-900" />
                  </div>
                  
                  <h1 className="text-3xl md:text-4xl font-bold tracking-tight mb-4 text-slate-900">
                     List Eksklusif.
                  </h1>
                  
                  <p className="text-slate-500 text-base mb-8 leading-relaxed max-w-xs mx-auto">
                     Kami menjaga komunitas tetap kecil dan berkualitas. Bergabunglah dengan waitlist untuk akses prioritas.
                  </p>

                  <form onSubmit={handleSubmit} className="relative z-20">
                     <div className="flex flex-col gap-3">
                        <div className="relative group">
                           <input 
                              type="email" 
                              required
                              value={email}
                              onChange={(e) => setEmail(e.target.value)}
                              placeholder="nama@perusahaan.com" 
                              className="w-full bg-slate-50 border border-slate-200 rounded-xl px-5 py-3.5 text-slate-900 placeholder:text-slate-400 focus:outline-none focus:bg-white focus:border-slate-900 focus:ring-1 focus:ring-slate-900 transition-all font-medium text-sm"
                           />
                           <div className="absolute right-4 top-1/2 -translate-y-1/2 text-slate-300 group-focus-within:text-slate-900 transition-colors">
                              <Lock className="w-4 h-4" />
                           </div>
                        </div>
                        
                        <button 
                           type="submit"
                           disabled={status === 'loading'}
                           className="w-full bg-slate-900 text-white font-bold text-sm py-3.5 rounded-xl hover:bg-black hover:shadow-lg hover:shadow-slate-900/20 transition-all active:scale-[0.98] flex items-center justify-center gap-2 group disabled:opacity-70"
                        >
                           {status === 'loading' ? (
                              <Loader2 className="w-4 h-4 animate-spin" />
                           ) : (
                              <>
                                 Gabung Waitlist
                                 <ArrowRight className="w-4 h-4 group-hover:translate-x-0.5 transition-transform" />
                              </>
                           )}
                        </button>
                     </div>
                  </form>

                  {/* Scarcity Indicator - Elegant Version */}
                  <div className="mt-8 pt-6 border-t border-slate-100 flex items-center justify-center gap-2 text-[11px] text-slate-500 font-medium">
                     <Hourglass className="w-3 h-3 text-slate-400" />
                     <span>
                        Hanya tersisa <span className="text-slate-900 font-bold font-mono">{spotsLeft}</span> slot minggu ini
                     </span>
                  </div>
               </div>
            )}

          </div>
        </div>

        {/* --- SOCIAL PROOF --- */}
        <div className="mt-12 flex items-center gap-3 opacity-60">
           <div className="flex -space-x-2">
              {[1,2,3].map((i) => (
                 <div key={i} className="w-6 h-6 rounded-full bg-white border border-slate-200 shadow-sm"></div>
              ))}
           </div>
           <span className="text-[10px] uppercase tracking-widest text-slate-400 font-bold">Joined recently</span>
        </div>

      </main>

      {/* --- FOOTER --- */}
      <footer className="relative z-10 py-6 text-center text-[10px] uppercase tracking-widest text-slate-400">
         &copy; 2025 Renunganku Inc.
      </footer>

    </div>
  );
}